* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(#111, #444);
  min-height: 100vh;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.title {
  text-align: center;
  margin-bottom: 40px;
  color: #fff;
  filter: drop-shadow(2px 2px 5px rgb(165, 24, 153))
    drop-shadow(-2px -2px 5px rgb(165, 24, 153));
}
.list {
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
}
.list,
.detiles {
  background-color: #fff;
  width: 60%;
  max-width: 700px;
  overflow: auto;
  max-height: 500px;
  margin: 0 auto;
  min-height: 500px;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
}
.detiles {
  display: flex;
  flex-direction: column;
  max-height: none;
}
.list-item {
  padding: 20px;
  cursor: pointer;
  transition: 0.3s all ease;
}
.list-item:nth-child(odd) {
  background: #eee;
}
.list-item:nth-child(even) {
  background: #c7c7c7;
}
.list-item:hover {
  background: linear-gradient(90deg, rgba(166, 98, 230, 0.521), transparent);
}
.list-item span {
  font-family: sans-serif;
  font-size: 14px;
}
.filter {
  min-height: 80px;
  background-color: #888;
  width: 60%;
  max-width: 700px;
  margin: 0 auto;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}
.header {
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgb(177, 177, 177);
  padding: 5px 10px;
}

.header h3 {
  text-align: center;
  flex-grow: 1;
}
.body {
  background: radial-gradient(
    at 50% 0,
    rgb(255, 255, 255) 15%,
    rgb(83, 29, 97)
  );
  padding: 10px;
  background: chartreusew;
  flex-grow: 1;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.body .avatar {
  width: 200px;
  height: 200px;
}
.body .avatar figure {
  width: 100%;
  height: 100%;
}

.body .avatar figure img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.body .info {
  flex-grow: 1;
  padding: 0 100px;
}

.card-row {
  display: flex;
  align-items: flex-end;
  margin-bottom: 10px;
}
.card-title {
  font-size: 16px;
  font-weight: bold;
  color: #111;
}
.card-value {
  font-size: 14px;
  color: #555;
}

.card-type {
  background-color: rgb(104, 104, 104);
  color: rgb(25, 22, 179);
  padding: 5px 10px;
  margin-right: 5px;
  border-radius: 25px;
  display: inline-block;
  cursor: pointer;
  margin-bottom: 10px;
}
.back-btn {
  cursor: pointer;
}
/* /////////////////loader////////////////// */
.lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
  position: absolute;
  top: calc(50% - 40px);
  left: calc(50% - 40px);
}
.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid rgb(66, 22, 170);
  border-color: rgb(79, 30, 214) transparent #fff transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.filter {
  display: flex;
  justify-content: center;
  align-items: center;
}
.inputbtn {
  width: 220px;
  height: 22px;
  overflow: hidden;
  outline: none;
  border-radius: 3px;
  border: none;
}
.select {
  height: 22px;
  border-radius: 3px;
  border: none;
  overflow: hidden;
  outline: none;
  margin: 0 5px;
}
.search {
  height: 22px;
  border-radius: 3px;
  border: none;
  box-shadow: 0 0 5px black;
  padding: 3px;
  font-size: 14px;
  font-weight: 500;
}
@media screen and (max-width: 425px) {
  .avatar {
    width: 100px;
    height: 100px;
  }
  .body .avatar {
    width: 100px;
    height: 100px;
  }
  .body .info {
    padding: 0;
  }
}
